html {
  font-size: 62.5%;
}

body {
  -webkit-font-smoothing: antialiased;
  font-family: 'Open Sans', Helvetica, sans-serif;
  color: #a5a5a5;
  background-color: #f3f2f2;
}

p::selection, h1::selection, h2::selection, a::selection {
  background-color: #eaeaea;
}

/* Layout */
.wrapper {
  margin: 0 auto;
  width: 720px;
}

@media only screen and (max-device-width: 890px), (max-width: 890px) {
  .wrapper {
    margin: 0 auto;
    width: 90%;
    padding: 0 5%;
  }
}

.header .wrapper {
  background-image: url(../img/gloggbadgebig.svg);
  padding-bottom: 59px;
  background-size: 59%;
  background-repeat: no-repeat;
  background-position: right center;
}

@media only screen and (max-device-width: 890px), (max-width: 890px) {
  .header .wrapper {
    background-size: 59%;
  }
}

@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .header .wrapper {
    padding-bottom: 26px;
    background-size: 59%;
  }
}

.header {
