  font-family: 'Raleway', Helvetica, sans-serif;
  font-size: 7.2rem;
  line-height: 7.4rem;
  color: #f3f2f2;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 20px;
  display: inline-block;
  float: left;
  max-width: 50%;
}

@media only screen and (max-device-width: 890px), (max-width: 890px) {
