  }
}

p.welcome-text {
  font-family: 'Bree Serif', serif;
  font-size: 1.6rem;
  padding-top: 40px;
  color: #f3f2f2;
  text-transform: uppercase;
  letter-spacing: 1.54px;
  margin-top: 0;
  margin-bottom: 0;
}

@media only screen and (max-device-width: 890px), (max-width: 890px) {
