.radios {
  display: block;
  margin: 52px 0;
  text-align: center;
  vertical-align: top;
}

@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .radios {
    margin: 10px 0;
  }
}

.inline {
  position: relative;
  display: block;
  margin-bottom: 15px;
  padding-left: 30px;
  cursor: pointer;
}

.inline:first-child {
  margin-right: 30px;
  margin-top: 20px;
}

@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .inline:first-child {
    margin-right: 8px;
  }
}

.inline input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.inline .inline-indicator {
  margin-right: 10px;
}

@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .inline .inline-indicator {
    margin-right: 0;
  }
}

.inline span.text {
  margin-left: 10px;
  font-size: 1.8rem;
}

@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .inline span.text {
    margin-left: 1px;
    font-size: 1.4rem;
  }
}

.inline-indicator {
  position: absolute;
  top: -4px;
  left: 0;
  width: 28px;
  height: 28px;
  background: #f3f2f2;
}

@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .inline-indicator {
    top: 1px;
    left: 0;
    width: 22px;
    height: 22px;
  }
}

.radios .inline-indicator {
  border-radius: 50%;
}

/* Hover and focus states */
.radios:hover input ~ .inline-indicator,
.radios input:focus ~ .inline-indicator {
  background: #f3f2f2;
}

/* Checked state */
.radios input:checked ~ .inline-indicator {
  background: #d02f52;
}

/* Disabled state */
.radios input:disabled ~ .inline-indicator {
  pointer-events: none;
  opacity: .6;
  background: #f3f2f2;
}

/* Check mark */
.inline-indicator:after {
  position: absolute;
  display: none;
  content: '';
}

/* Show check mark */
.radios input:checked ~ .inline-indicator:after {
  display: block;
}

/* Checkbox tick */
.radios .inline-indicator:after {
  top: 4px;
  left: 8px;
  width: 3px;
  height: 8px;
  transform: rotate(45deg);
  border: solid #f3f2f2;
  border-width: 0 2px 2px 0;
}

/* Disabled tick colour */
.radios input:disabled ~ .inline-indicator:after {
  border-color: #f3f2f2;
}

/* Radio button inner circle */
.radios .inline-indicator:after {
  top: 8px;
  left: 8px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #f3f2f2;
}

@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .radios .inline-indicator:after {
    top: 7px;
    left: 7px;
    width: 6px;
    height: 6px;
  }
}

/* Disabled circle colour */
.radios input:disabled ~ .inline-indicator:after {
  background: #f3f2f2;
}

