@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .copyright {
    font-size: 1rem;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

p.copyright a {
  text-decoration: none;
  cursor: pointer;
  color: #a5a5a5;
}

p.copyright a:hover {
  color: #d74d6a;
}

/* Other media queries */
/* Tablet-sized devices */
@media only screen and (max-device-width: 890px), (max-width: 890px) {
  .center-small {
    margin-top: 20px;
    margin-bottom: 10px;
    text-align: center;
  }
  .content {
    margin-bottom: 40px;
  }
}

/* Mobile-sized devices */
@media only screen and (max-device-width: 470px), (max-width: 470px) {
  .content {
    margin-bottom: 20px;
  }
}
